.scout-invest {
  padding: 90px 20px 55px 20px;

  @include media(">=large") {
    padding: 60px 20px 20px 155px;
  }

  @include media(">=large", "<=xlarge") {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  @include media(">=xlarge") {
    padding: 130px 20px 20px 375px;
  }

  @include media(">=2000px") {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  ul {
    display: flex;
    align-items: center;
    margin: 0;

    @include media("<=large") {
      flex-wrap: wrap;
      justify-content: center;
      flex-direction: row;
    }

    @include media("<=small") {
      flex-direction: column;
    }

    li{
      background-color: $light-gray;
      padding: 0;
      margin-bottom: 20px;

      @include media("<=large") {
        margin: 10px;
      }

      @include media(">=large") {
        margin-bottom: 0;
      }

      &:last-child {
        //margin-bottom: 0;
        //margin-right: 0;
      }

      &:before {
        display: none;
      }
    }
  }

  .scout-invest-wrap {

    @include media("<=medium") {
      margin-bottom: 45px;
    }

    .title{
      @include media(">=large") {
        max-width: 25.5%;
      }

      @include media(">=xlarge") {
        max-width: 19%;
        margin-bottom: 60px;
      }

    }

    .listing {
      position: relative;

      @include media(">=large") {
        margin-bottom: 70px;
      }

      @include media(">=xlarge") {
        padding-left: 40px;
        margin-bottom: 170px;
      }

      img {
        margin-bottom: -30px;
        display: block;
      }


    }
    li {
      padding: 14px;
      text-align: center;
      width: 200px;
      height: 200px;
      display: flex;
      align-items: center;
      justify-content: space-around;
      flex-direction: column;


      @include media("<=large") {
        flex: 0 0 30%;
      }

      @include media(">=large") {
        margin-right: 15px;
      }

      @include media(">=xlarge") {
        margin-right: 30px;
      }


      span {
        font-family: $font-family-bold;
        width: 100%;
        @include media(">=large") {
          font-size: 16px;
        }
      }
    }
  }

  .universities-wrap{

    .title{

      @include media(">=xlarge") {
        margin-bottom: 40px;
      }
    }

    li{
      width: 255px;
      height: 140px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-left: 4px solid $primary-color;

      @include media("<=large") {
            flex: 0 0 30%;
      }

      @include media(">=large") {
        height: 138px;
        width: 337px;
        margin-right: 25px;
      }

      @include media(">=xlarge") {
        width: 367px;
        height: 150px;
        margin-right: 30px;
      }
    }
  }

  .substrate {

    @include media(">=large") {
      width: 74.5%;
      background-color: $primary-color;
      height: 400px;
      position: absolute;
      right: 0;
      z-index: -1;
      top: 50%;
      transform: translateY(-50%);
      background-image: url('../images/pattern-decoration-13.svg');
      background-size: contain;
      background-repeat: no-repeat;
      background-position: right center;

    }
    @include media(">=xlarge") {
      width: 81%;
      background-image: url('../images/pattern-decoration-lg.svg');
    }
  }
}