.investors {
	padding-bottom: 24px;
	background-color: $light-gray;
	color: $dark-gray;
	@include flex(flex, center, center);
	h2, .h2 {
		line-height: 48px;
	}
	.col {
		margin-bottom: 32px;
		@include media(">large") {
			margin-bottom: 0;
		}
		&:last-child {
			.investor:after {
				display: none;
			}
		}
		&:nth-child(even) {
			.investor:after {
				@include media("<small") {
					display: none;
				}
			}
		}
	}
	.title {
		color: $white;
		margin-bottom: 70px;
		padding-top: 90px;
		@include media(">=large") {
			padding-top: 0;
			margin-bottom: 104px;
		}

		@include media(">=xlarge") {
			margin-bottom: 140px;
		}

		@media (min-width: 1200px) and (max-height: 768px) {
			margin-bottom: 100px;
		}
	}
}
.investor {
	position: relative;
	padding: 0 36px;
	height: 100%;
	//@include flex(flex, center, center);
	@include media(">=large") {
		padding: 0 49px;
	}
	@include media(">=xlarge") {
		padding: 0 60px;
	}
	&:after {
		content: '';
		position: absolute;
		top: 50%;
		right: 0;
		transform: translateY(-50%);
		width: 1px;
		height: 49px;
		background-color: rgba(#383838, .4);
	}
	img {
		max-width: 100%;
		height: auto;
	}
}
.investors-row {
	margin-bottom: 40px;
	@include media(">=large") {
		margin-bottom: 14px;
	}
	@include media(">=xlarge") {
		margin-bottom: 40px;
	}
	@include media(">=xxlarge") {
		margin-bottom: 85px;
	}
	&:last-child {
		margin-bottom: 0;
	}
	h3, .h3 {
		margin-bottom: 75px;
		@include media(">=large") {
			margin-bottom: 65px;
		}
		@include media(">=xlarge") {
			margin-bottom: 75px;
		}
		@media (min-width: 1200px) and (max-height: 768px) {
			margin-bottom: 25px;
		}
	}
}
.investors-wrap {
	position: relative;
	width: 100%;

	.section-content{
		@include media(">=xxlarge") {
			margin-bottom: -40px;
			@media (-ms-high-contrast: none), (-ms-high-contrast: active) {
				margin-bottom: -90px;
			}
		}
	}
	.decoration-block {
		position: absolute;
		top: 50px;
		left: 0;
		width: 100%;
		height: 120px;
		background-color: $primary-color;
		background-image: url('../images/spica-white.png');
		background-position: right center;
		background-repeat: no-repeat;
		background-size: contain;
		@include media(">=large") {
			top: -50px;
			width: calc(100% / 3);
			min-width: 525px;
			height: 150px;
			background-image: url('../images/spica-half.png');
		}
		@media (min-width: 1200px) and (max-height: 800px) {
			top: -48px;
		}
		@media (min-width: 1200px) and (max-height: 768px) {
			top: -28px;
			height: 120px;
		}


		@include media(">xlarge") {
			width: 645px;
		}

		@include media(">=2000px") {
			width: calc(50% - 115px);
		}
	}
}
.investors-items {
	@include flex(flex, start, center, wrap);
	margin-left: -25px;
	margin-right: -25px;
	@include media(">=small") {
		flex-basis: 20%;
		flex-wrap: nowrap;
	}
	.investors-item {
		flex-basis: 50%;
		margin-bottom: 36px;
		@media (min-width: 480px) {
			flex-basis: calc(100% / 3);
		}

		@include media(">=xxlarge") {
			margin-bottom: 0;
		}

		@include media(">=small") {
			flex-basis: 20%;
		}

		&:last-child {
			.investor:after {
				display: none;
			}
		}
		&:nth-child(even) {
			.investor:after {
				display: none;
				@media (min-width: 480px) {
					display: block;
				}
			}
		}
		&:nth-child(3n) {
			@include media(">=small") {
				flex-basis: 22.5%;
			}
			@include media(">=xlarge") {
				flex-basis: 23%;
			}
			.investor:after {
				display: block;
				@media (min-width: 480px) {
					display: none;
				}
				@include media(">=small") {
					display: block;
				}
			}
		}
	}
}