.fullpage-wrapper {
  visibility: hidden;
  .section {
    @include media("<=large") {
      height: auto !important;
    }
  }

  @include media(">=large") {
    .active{
      .an-short:not(.done){
        animation-delay: 2s;
        animation: fadeAnimation .6s linear;
      }

      .an-medium:not(.done){
        animation-delay: 1.5s;
        animation: fadeAnimation .8s linear;
      }

      .an-long:not(.done){
        animation: fadeAnimation 1s linear;
      }
    }
  }
}

@keyframes fadeAnimation {
  from {
    opacity: 0;
    transform: translateY(500px)
  }
  to{
    opacity: 1;
    transform: translateY(0)
  }
}

#fp-nav {
  left: 40px;
  margin-top: 0!important;

  li {
    padding-left: 0;
    margin: 12px 0;
    position: relative;

    &:before {
      display: none;
    }

    &:last-child {
      display: none;
    }

    span {
      opacity: 0.6;
      font-size: 14px;
      color: #000;
    }

    a {

      &:before {
        content: '';
        width: 0px;
        height: 2px;
        background-color: $black;
        left: 0;
        bottom: -10px;
        position: absolute;
        display: block;
        transition: width .4s ease;
      }
    }

    a.active {
      margin-bottom: 20px;

      &:before {
        width: 30px;
      }

      span {
        font-family: $font-family-bold;
        font-size: 18px;
        opacity: 1;
      }
    }
  }
}