.agtech-support {
  padding-left: 20px;
  position: relative;
  margin-bottom: -50px;

  @include media(">=large") {
    margin-bottom: 0;
    padding-left: 200px;
    padding-top: 13vh;
  }

  @include media(">=xlarge") {
    padding-left: 375px;

  }

  @media (min-width: 1200px) and (max-height: 900px) {
    padding-left: 155px;
    padding-top: 10vh;
  }

  .content-wrap {
    @include media(">=large") {
      display: flex;
    }
  }

  .description-holder {
    padding-right: 15px;

    @include media(">=large") {
      padding-right: 100px;
      flex: 0 0 38%;
    }

    @media (min-width: 1200px) and (max-height: 900px) {
      padding-right: 30px;
      flex: 0 0 44%;
    }
  }

  .image-holder {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 295px;
    flex: 0 0 100%;
    margin-left: 15%;

    @include media(">=medium") {
      height: 60vh;
    }

    @include media(">=large") {
      margin-left: 0;
      height: 87vh;
      flex: 0 0 62%;
    }

    @include media(">=xlarge") {
      height: 87vh;
    }

    @media (min-width: 1200px) and (max-height: 900px) {
      height: 90vh;
      flex: 0 0 56%;
    }
  }

  .descr-banner {
    max-width: 280px;
    border-left: 4px solid $primary-color;
    position: relative;
    top: -100px;
    background-color: $light-gray;
    margin: 0 auto;

    @include media(">=medium") {
      top: auto;
      position: absolute;
      max-width: 970px;
      bottom: 90px;
      display: flex;
      max-height: 185px;
    }

    @include media(">=xxlarge") {
      max-width: 1170px;
      max-height: 260px;
    }

    @media (min-width: 1200px) and (max-height: 900px) {
      max-width: 970px;
      bottom: 30px;
    }

    @include media(">=2000px") {
      max-width: 80%;
    }


    strong {
      text-transform: uppercase;
    }

    p {
      margin: 0;
    }

    .item {
      padding: 25px 25px 40px 25px;

      @include media(">=large") {
        font-size: 22px;
        flex: 0 0 50%;
      }

      @include media(">=large") {
        &:nth-child(odd) {
          padding: 40px 20px 40px 70px;
          flex: 0 0 50%;
        }
        &:nth-child(even) {
          padding: 40px 70px 40px 20px;
          flex: 0 0 50%;
        }
      }
      @include media(">=xxlarge") {
        p{
          font-size: 20px;
        }
        //padding: 70px 100px;
        &:nth-child(even) {
          padding: 70px 100px 70px 100px;
        }
        &:nth-child(odd) {
          padding: 70px 165px 70px 100px;
        }
      }
    }
  }
  .slick-arrow {
    display: none !important;
  }
}

.slick-dots {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -20px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  li {
    cursor: pointer;
    position: relative;
    margin: 0 5px;
    width: 21px;
    height: 21px;
    &:before {
      display: none;
    }
    &:after {
      content: '';
      height: 1px;
      width: 100%;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      background-color: #383838;
      opacity: 0.4;
      display: block;
    }
    button {
      display: none;
    }
  }
  .slick-active {
    &:after {
      opacity: 1;
      height: 2px;
    }
  }
}

