.footer {
  background-color: $dark-gray;
  color: $white;

  h6 {
    font-size: 20px;
    text-align: center;
    margin-bottom: 35px;

    @include media(">=medium") {
      text-align: left;
      margin-bottom: 0;
    }
  }

  .social-icon {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $primary-color;

    @include media("<=medium") {
      margin: 0 auto 40px auto;
    }

    @media (min-width: 1024px) and (max-height: 976px) and(orientation: landscape) {
      margin: 0;
    }

    @include media(">=medium") {
      margin-left: 40px;
      width: 45px;
      height: 45px;
    }
  }

  .copyright-block {
    padding: 50px 55px;
    text-align: center;
    @include media(">=medium") {
      font-size: 16px;
    }
  }

  p {
    margin-bottom: 0;
  }

  .text-right {
    text-align: right;
    display: flex;
    flex-direction: column-reverse;

    @include media(">=medium") {
      flex-direction: row;
      align-items: center;
      justify-content: flex-end;
    }

    address {
      font-style: normal;

      @include media("<=640px") {
        text-align: left;
      }

      @include media("<=medium") {
        text-align: center;
      }

      @include media(">=xlarge") {
        font-size: 20px;
      }
    }

  }

  .additional-info {
    padding: 50px 55px;
    position: relative;

    @include media(">=xlarge") {
      padding: 50px 100px;
    }

    &:after {
      left: 0;
      content: '';
      position: absolute;
      width: 100%;
      bottom: 0;
      height: 1px;
      background-color: #fff;
      opacity: 0.2;
    }

  }
}