// variables

// colors
$primary-color: #B4CF3D;
$secondary-color: #252525;

$white: #fff;
$light-gray: #F7F7F7;
$medium-gray: #bbb;
$dark-gray: #393939;
$black: #000;
$danger-color: red;
$warning-color: yellow;
$success-color: green;

$font-family-light: "Avenir LT W01_45 Book1475508", "Helvetica", sans-serif;
$font-family-medium: "Avenir LT W01_85 Heavy1475544", "Helvetica", sans-serif;
$font-family-bold: "Avenir LT W01_95 Black1475556", "Helvetica", sans-serif;

$main-fz: 16px;
$main-lh: 1.5;
$main-ff: $font-family-light;
$main-text-color: $black;
$headings-lh: 1.2;
$main-margin: $main-fz * $main-lh;

// grid settings
$gl-gridName: grid;
$gl-colName: col;
$gl-attributeName: class;
$gl-gutter: 30px; // Total left + right
$gl-gutter-vertical: 0;
$gl-mq-width: 'min-width';
$gl-mq-list: (
        xs: 320px,
        sm: 768px,
        md: 1024px,
        lg: 1200px,
        xl: 1440px,
        xxl: 1600px
);

// media settings
$breakpoints: (small: 768px, medium: 1024px, large: 1200px, xlarge: 1440px, xxlarge: 1600px);

body {
  font: #{$main-fz}/#{$main-lh} $main-ff;
  color: $main-text-color;
  background: $white;
}

h1, h2, h3, h4, h5 {
  //font-weight: 700;
  font-family: $font-family-medium;
}

h1 {
  margin-bottom: #{24px * $headings-lh};
  line-height: $headings-lh;
  font-size: 38px;

  @include media(">=large") {
    font-size: 50px;
  }
}

h2 {
  margin-bottom: #{32px * $headings-lh};
  font-size: 32px;
  line-height: $headings-lh;
  @include media(">=large") {
    font-size: 44px;
  }
}

h3 {
  margin-bottom: #{20px * $headings-lh};
  font-size: 24px;
  line-height: $headings-lh;
  @include media(">=large") {
    font-size: 34px;
  }
}

h4 {
  margin-bottom: #{18px * $headings-lh};
  font-size: 18px;
  line-height: $headings-lh;
  @include media(">=large") {
    font-size: 32px;
  }
}

h5 {
  margin-bottom: #{16px * $headings-lh};
  font-size: 18px;
  line-height: 1.5;
  //line-height: $headings-lh;
  text-transform: uppercase;
}

h6 {
  margin-bottom: #{14px * $headings-lh};
  font-size: 14px;
  line-height: $headings-lh;
}

p {
  margin-bottom: $main-margin;
}

a {
  text-decoration: none;
  color: $primary-color;
  outline: none;
  &:hover {
    color: darken($primary-color, 10%);
  }
}

ul {
  margin-bottom: $main-margin;
  li {
    padding-left: 20px;
    &:before {
      content: "";
      float: left;
      margin: 8px 0 0 -20px;
      border-radius: 50%;
      width: 6px;
      height: 6px;
      background: $black;
    }
  }
}

ol {
  margin-bottom: $main-margin;
  counter-reset: counterName;
  li {
    &:before {
      counter-increment: counterName;
      content: counters(counterName, "-") ". ";
    }

  }
  ul,
  ol {
    margin: 0;
    padding-left: 20px;
  }
}

// forms

input[type="text"],
input[type="password"],
input[type="email"],
input[type="tel"],
input[type="number"] {
  display: inline-block;
  margin: 0 0 $main-margin;
  border: none;
  font-family: $main-ff;
  border-bottom: 1px solid $secondary-color;
  padding: 5px 20px 5px 0;
  line-height: 30px;
  font-size: 16px;
  height: 42px;
  vertical-align: middle;
  background: $white;
  outline: none;
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  opacity: 0.4;
  transition: opacity .5s ease;
  @include placeholder {
    color: $black;
  }
  &:focus {
    opacity: 1;
  }
}

select {
  display: inline-block;
  margin: 0 0 $main-margin;
  border: 1px solid $light-gray;
  padding: 5px 20px;
  line-height: 30px;
  font-size: 16px;
  height: 42px;
  vertical-align: middle;
  background: $white;
  outline: none;
  &:focus {
    border-color: $primary-color;
  }
}

.button,
input[type="submit"],
button {
  display: inline-block;
  margin: 0 0 $main-margin;
  border: 1px solid transparent;
  padding: 5px 20px;
  line-height: 30px;
  font-size: 16px;
  height: 42px;
  color: $white;
  vertical-align: middle;
  text-decoration: none;
  white-space: nowrap;
  background: $primary-color;
  cursor: pointer;
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  &:hover,
  &:focus {
    color: $white;
    background: darken($primary-color, 10%);
  }
  &.secondary {
    background: $secondary-color;
    &:hover,
    &:focus {
      background: darken($secondary-color, 10%);
    }
  }
  &.danger {
    background: $danger-color;
    &:hover,
    &:focus {
      background: darken($danger-color, 10%);
    }
  }
  &.warning {
    background: $warning-color;
    &:hover,
    &:focus {
      background: darken($warning-color, 10%);
    }
  }
  &.success {
    background: $success-color;
    &:hover,
    &:focus {
      background: darken($success-color, 10%);
    }
  }
}

input[type="checkbox"] {
  display: inline-block;
  margin: 0 0 $main-margin;
  vertical-align: middle;
}

input[type="radio"] {
  display: inline-block;
  margin: 0 0 $main-margin;
  vertical-align: middle;
}

label {
  display: inline-block;
  margin: 0 0 $main-margin;
  vertical-align: middle;
}

blockquote {
  margin: 0 0 $main-margin;
  p {
    margin: 0;
  }
  q {
    quotes: none;
    &:before,
    &:after {
      quotes: none;
    }
  }
  cite {
    display: block;
  }
}

table {
  margin: 0 0 $main-margin;
  th,
  td {
    border: 1px solid $medium-gray;
    padding: 5px;
  }
}

[class^="col"] {
  > * {
    //background: rgba(147, 128, 108, 0.2);
  }

}