.success-stories {

  @include media("<=medium") {
    padding-bottom: 110px;
  }

  @include media(">=large") {
    display: flex;
    justify-content: center;
    flex-direction: column;
  }

  @include media(">=xlarge") {
    display: block;
    padding-top: 100px;
  }

  @include media(">=2000") {
    display: flex;
  }

  .slick-dots {
    bottom: -60px;
  }

  .title-decoration {
    overflow: hidden;
    background-color: $primary-color;
    background-image: url('../images/success-stories-decoration.svg');
    background-repeat: no-repeat;
    background-position: right center;
    background-size: contain;

    @include media("<=small") {
      max-width: 245px;
      padding: 30px 20px;
      margin-bottom: 175px;
    }

    @include media(">=medium") {
      padding-left: 155px;
      margin-bottom: 50px;
      height: 160px;
      display: flex;
      align-items: center;
      width: 45%;
    }

    @include media(">=xlarge") {
      padding-left: 375px;
      height: 200px;
      margin-bottom: 230px;
    }

    h2 {
      margin: 0;
      color: $white;
    }
  }

  .image-holder {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;

    @include media("<=large") {
      width: 78vw;
      height: 490px;
    }

    @include media(">=large") {
      width: 55%;
      height: 80vh;
    }

  }

  .listing {

    ul {
      @include media("<=small") {
        max-width: 280px;
        margin: 0 auto;
      }
    }

    @include media(">=large") {
      padding-left: 155px;
      padding-right: 55px;
      margin: 0 auto;
    }

    @include media(">=xlarge") {
      padding: 0;
      max-width: 1170px;
      margin: 0 auto;
    }
  }

  .slick-slide {
    @include media(">=medium") {
      margin: 0 12px;

      &:last-child {
        margin-right: 0;
      }
    }
    @include media(">=xlarge") {
      margin-right: 30px;

    }
  }

  ul {

    img {
      display: block;
      max-width: 130px;
      margin: 0 auto 50px auto;
    }

    a {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 70px;
      background-color: $primary-color;
      display: flex;
      align-items: center;
      color: $white;
      padding: 0 30px;

      @include media(">=large") {
        padding: 0 40px;
      }

      @include media(">=xlarge") {
        font-size: 20px;
      }

      &:after {
        content: '';
        right: 30px;
        position: absolute;
        width: 0;
        height: 0;
        top: 50%;
        transform: translateY(-50%);
        border-style: solid;
        border-width: 6px 0 6px 7px;
        border-color: transparent transparent transparent #ffffff;

        @include media(">=large") {
          right: 40px;
        }
      }
    }
  }

  .slick-arrow {
    display: none !important;
  }

  li{
    &:before {
      display: none;
    }
  }

  .item-content {
    position: relative;
    background-color: #f8f8f8;
    padding: 20px 50px 100px 60px;
    height: 350px;
    overflow: hidden;

    p{
      text-align: left;
    }

    img{
      text-align: center;
    }

    @include media(">=large") {
      padding: 30px 50px 100px 50px;
    }

  }
}