.what-we-invest{
  background-repeat: no-repeat;
  background-position: left center;
  padding-top: 50px;

  .title{

    h2{
      max-width: 70%;
    }
    @include media("<=large") {
      padding-left: 20px;
      position: relative;
    }
  }

  @include media("<=medium") {
    background-image: none!important;
  }

  @include media(">=large") {
    padding-top: 0;
    display: flex;
    align-items: center;
  }
  
  @include media(">=xlarge") {
    background-position: 150px center;
  }

  .section-wrap{

    @include media(">=large") {
      padding-left: 160px;
    }
    @include media(">=xlarge") {
      padding-left: 375px;
      margin-top: -100px;
    }
  }

  h4{
    font-size: 22px;
    margin-bottom: 15px;

    @include media(">=large") {
      margin-bottom: 25px;
      font-size: 26px;
    }
  }

  ul{
    margin: 0;

    li{
      @include media("<=medium") {
        font-size: 14px;
      }

      @include media(">=xlarge") {
        font-size: 19px;
        &:before{
          margin-top: 11px;
        }
      }
    }
  }

  .list-block{
    background-color: $primary-color;
    padding: 40px 40px 40px 20px;
    display: inline-block;
    margin-top: 175px;

    @include media(">=768px") {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }

    @include media(">=large") {
      position: relative;
      top: auto;
      transform: none;
      padding: 60px 100px 60px 160px;
      margin-left: -160px;
      margin-top: 0;
    }

    @include media(">=xlarge") {
      margin-left: -375px;
      padding: 60px 150px 65px 375px;
    }
  }

  .list{
    margin-bottom: 25px;

    @include media(">=768px") {
      display: inline-block;
      margin-right: 90px;
      margin-bottom: 0;
    }


    @include media(">=large") {

    }

    @include media(">=xlarge") {
      margin-right: 150px;
    }

    &:last-child{
      margin: 0;
    }

  }

  .image-holder{
    height: 75vh;
    width: 80vw;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: absolute;
    right: 0;
    z-index: -1;

    @include media(">=768px") {
      position: relative;
      margin-left: auto;
    }

    @include media(">=large") {
      position: absolute;
      top: 0;
      height: 100vh;
      width: 48vw;
    }
  }
}