.main-start-banner {

  .content-wrap {
    display: flex;
    align-items: center;
    flex-direction: column;

    @include media(">=large") {
      justify-content: center;
      flex-direction: row;
    }
  }

  .description-holder {
    color: $medium-gray;
    padding: 30px 20px 0 20px;

    h3{
      @include media(">=xlarge") {
        line-height: 46px;
      }
    }

    @include media(">=large") {
      padding: 0 40px 0 155px;
      flex: 0 0 45%;
      max-width: 45vw;
    }

    @include media(">=xxlarge") {
      padding: 0 40px 0 375px;
    }

    @media (min-width: 1200px) and (max-height: 900px) {
      padding: 0 30px 0 155px;
      flex: 0 0 48%;
    }

  }

  .pattern{
    display: block;
    background-color: $light-gray;
  }

  .pattern.rect{

    position: absolute;
    bottom: 35px;
    width: 60px;
    height: 47px;
    left: -60px;

    @include media(">=large") {
      top: calc(100vh - 267px);
      width: 300px;
      height: 137px;
      left: -300px;
    }

    @media (min-width: 1200px) and (max-height: 900px) {
      top: calc(100vh - 220px);
      width: 170px;
      height: 90px;
      left: -170px;
    }
  }

  .pattern.long{
    width: 225px;
    height: 20px;

    @include media(">=medium") {
      position: absolute;
      height: 30px;
      top: 156px;
      left: 0;
    }

    @include media(">=xlarge") {
      width: 545px;
    }

    @media (min-width: 1200px) and (max-height: 900px) {
      top: 105px;
    }
  }

  .image-holder {
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 39vh;
    width: 62%;
    position: relative;
    right: -21%;
    z-index: 1;

    @media screen and (orientation:landscape) {
      height: 250px;
    }

    @media screen and (min-width: 768px) and (max-width: 1024px) and (orientation:landscape) {
      height: 350px;
    }

    @include media(">=large") {
      right: 0;
      height: 94vh;
      flex: 0 0 55%;
      width: 100%;
    }

    @media screen and (min-width: 1200px) and (max-height: 900px){
      height: 90vh;
      flex: 0 0 52%;
    }

    @include media(">=2000px") {
      height: 96vh;
    }

  }

  .scroll-down-block {
    height: 65px;
    z-index: - 1;
    color: $white;
    display: flex;
    align-items: center;
    width: 100%;
    background-color: $primary-color;
    transform: translateY(-35px);
    padding: 30px;

    @include media(">=large") {
      transform: none;
      padding-left: 155px;
      position: absolute;
      bottom: 0;
      left: 0;
      height: 130px;
    }

    @include media(">=xxlarge") {
      padding-left: 375px;
    }

    span {
      &:after {
        content: '';
        width: 1px;
        height: 19px;
        left: 50%;
        display: block;
        background-color: $white;
        transform: translateX(-50%);
        position: relative;
      }
    }
  }
}