.error-wrap{
  height: 100vh;
  background-size: cover;
  background-position: center;
  position: relative;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;

  .error-banner{
    span{
      font-size: 20px;
      margin-bottom: 35px;
      font-family: $font-family-medium;
      @include media(">=medium") {
        font-size: 30px;
      }
    }

    h1{
      font-size: 70px;
      margin-bottom: 10px;
      @include media(">=medium") {
        font-size: 144px;
        margin-bottom:  20px;
      }
    }

    p{
      @include media(">=medium") {
        font-size: 24px;
        margin-bottom: 70px;
      }
    }
  }

  .error-inner{
    text-align: center;
    background-color: $primary-color;
    background-image: url('../images/404-decoration.svg');
    background-size: contain;
    background-position: left top;
    background-repeat: no-repeat;
    color: $white;
    padding: 50px 20px 70px 20px;

    @include media(">=medium") {
      position: absolute;
      top: 0;
      left: 150px;
      width: 550px;
      padding: 80px 60px 55px 60px;
    }

    @include media(">=xxlarge") {
      padding: 150px 120px 85px 120px;
      left: 250px;
    }
  }

  a{
    font-family: $font-family-medium;
    background-color: transparent;
    position: relative;
    color: $white;
    border-bottom: 2px solid $white;
    padding-bottom: 18px;
    padding-right: 40px;
    @include media(">=medium") {
      font-size: 24px;
    }

    &:after{
      content: '';
      right: 0;
      position: absolute;
      width: 0;
      height: 0;
      top: 5px;
      border-style: solid;
      border-width: 6px 0 6px 7px;
      border-color: transparent transparent transparent #ffffff;

      @include media(">=medium") {
        top: 10px;
      }
    }
  }
}