.header {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;

    @include media(">=large") {
        position: absolute;
        top: 0;
        left: 0;
        padding: 50px;
    }

    img{
        max-width: 220px;

        @include media(">=large") {
            max-width: 366px;
        }
    }
}