.directors {
	padding: 30px 0;
	background-position: calc(100% + 150px) center;
	background-repeat: no-repeat;
	background-size: 0;
	@include flex(flex, center, center);
	@include media(">large") {
		padding: 0;
		background-size: contain;
	}
	.section-content {
		position: relative;
	}
	.title {
		color: $white;
		margin-bottom: 60px;
	}
	.slider {
		.slick-arrow {
			background-color: $light-gray;
			@include media(">large") {
				box-shadow: 2px 2px 2px 0px $white;
			}
			&:hover {
				background-color: lighten($primary-color, 30%) 
			}
		}
		.slick-next{
			@include media(">=xlarge") {
			    
			}
		}
		.slick-disabled{
			&:hover{
				background-color: $light-gray;
			}
		}
	}
	.card-item {
		color: $dark-gray;
		font-size: 12px;
		font-weight: 300;
		line-height: 18px;		
		@include media(">large") {
			padding: 0 8px;
		}
		@include media(">=xlarge") {
			padding: 0 15px;
		}
		.image-holder {
			position: relative;
			height: 300px;
			@include bg(center, center, cover);
			z-index: 2;
			img {
				opacity: 0;
				width: 100%;
				height: 100%;
			}
		}
		.name {
			text-transform: none;
			margin-bottom: 5px;
		}
	}
	.item-content {
		position: relative;
		background-color: $light-gray;
		&:after {
			content: '';
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			width: 4px;
			background-color: $primary-color;
		}
	}
	.text-holder {
		padding: 30px 24px 25px 20px;
	}
}

.directors-wrap {
	position: relative;
	width: 100%;
	.decoration-block {
		position: absolute;
		top: -30px;
		left: 0;
		width: 100%;
		height: 285px;
		background-color: $primary-color;
		background-image: url('../images/spica-white.png');
		background-position: left center;
		background-repeat: no-repeat;
		background-size: contain;
		@include media(">large") {
			top: -80px;
			width: calc(50% - 70px);
			height: 320px;
			background-position: calc(50% - 70px) center;
		}
		@media (min-width: 1200px) and (max-height: 800px) {
			top: -35px;
		}
		@include media(">xlarge") {
			width: calc(50% - 115px);
		}
	}
}