.why-agtech {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  .why-agtech-wrap{
    padding: 50px 20px 75px 20px;
  }

  @include media(">=large") {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  .image-holder {
    min-height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;

    @include media(">=large") {
      height: 130px;
    }
  }

  h2 {

    @include media(">=large") {
      margin-bottom: 70px;
    }

    @media (min-width: 1200px) and (max-height: 900px) {
      margin-bottom: 60px;
    }
  }

  p {
    margin: 0;
  }

  .slick-arrow{
    display: none!important;
  }

  .items-list {
    max-width: 250px;
    margin: 0 auto;

    @include media(">=640px") {
      max-width: 600px;
    }

    @include media(">=large") {
      max-width: 1170px;
    }

    @media (min-width: 1200px) and (max-height: 900px) {
      max-width: 970px;
    }

    li {

      &:before {
        display: none;
      }
    }

    .item-content {
      border-top: 4px solid $primary-color;
      background-color: #fff;
      padding: 0 25px 25px 25px;

      @include media(">=large") {
        padding: 0 45px 45px 45px;
        min-height:410px;
        max-height: 420px;
        overflow-y: hidden;
      }

      @media (min-width: 1200px) and (max-height: 900px) {
        min-height: 388px;
        max-height: 390px;
        padding: 0 30px 40px 30px;
      }
    }
  }

  .slick-dots {
    bottom: -60px;
    li {
      &:after {
        background-color: #fff;
      }
    }
  }

  img {
    max-width: 61px;
    max-height: 57px;
  }
}