.contact-us-wrap{
  background-color: $light-gray;
  padding: 50px 20px;

  @include media(">=medium") {
    display: flex;
  }

  @include media(">=large") {
    padding: 60px;
  }

  @include media(">=xlarge") {
    align-items: center;
    padding: 100px;
  }

  h4{

    @include media("<=large") {
      font-size: 22px;
      margin-bottom: 35px;
    }

    @include media(">=large") {
      margin: 0;
    }
  }

  h6{
    font-size: 18px;
    margin-bottom: 5px;
    @include media(">=large") {
      font-size: 24px;
      margin-bottom: 10px;
    }
  }

  .grid{
    &:nth-child(1){

      @include media(">=large") {
        flex: 0 0 54%;
      }

      @include media(">=xlarge") {
        flex: 0 0 40%;
      }

      @include media(">=2000px"){
        padding-right: 60px;
      }
    }
    &:nth-child(2){

      @include media(">=large") {
        flex: 0 0 45%;
        padding-left: 100px;
      }

      @include media(">=xlarge") {
        padding-left: 0;
        flex:0 0 60%;
      }
    }

    .mail-block{
      @include media(">=xlarge") {
        margin-right: 60px;

        &:last-child{
          margin-right: 0;
        }
      }
    }
  }

  .mail-block{
    position: relative;
    padding-top: 15px;
    margin-bottom: 35px;

    @include media(">=large") {
      padding-top: 30px;
      margin-bottom: 50px;
    }

    &:last-child{
      margin-bottom: 0;
    }

    @include media(">=xlarge") {
      margin-bottom: 0;
    }

    &:before{
      content: '';
      width: 40px;
      height: 3px;
      background-color: $primary-color;
      display: block;
      position: absolute;
      top: 0;
      left: 15px;

      @include media(">=medium") {
        top: 0;
      }
    }

    a{
      color: $black;
      text-decoration: underline;

      @include media(">=large") {
        font-size: 24px;
      }
    }
  }
}