.team {
	padding: 60px 0 85px;
	background-color: $light-gray;
	background-position: calc(50% - 600px) center;
	background-repeat: no-repeat;
	background-size: contain;
	@include flex(flex, center, center);
	@include media(">large") {
		padding: 0;
	}
	.card-item {
		color: $dark-gray;
		font-size: 14px;
		font-weight: 300;
		line-height: 20px;		
		@include media(">large") {
			padding: 0 8px;
		}
		@include media(">xlarge") {
			padding: 0 15px;
		}
		.image-holder {
			height: 350px;
			margin-bottom: 22px;
			@include bg(center, center, cover);
			img {
				opacity: 0;
				width: 100%;
				height: 100%;
			}
		}
		.name {
			text-transform: none;
			margin-bottom: 5px;
		}
	}
	.item-content {
		position: relative;
		background-color: $white;
		&:after {
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 4px;
			background-color: $primary-color
		}
	}
	.text-holder {
		padding: 0 30px 20px;
	}
}
.section-content {
	position: relative;
	width: 100%;
	margin: 0 auto;
	padding: 0 25px;
	@include media(">large") {
		max-width: 970px;
		padding: 0 8px;
	}
	@include media(">xlarge") {
		max-width: 1200px;
		padding: 0 15px;
	}
	.title {
		margin-bottom: 76px;
		max-width: 60%;
		@include media(">large") {
			margin-left: -8px;
		}
		@include media(">=xlarge") {
			margin-left: 0;
		}
	}
}

.section-content.team-wrap{
	@media (-ms-high-contrast: none), (-ms-high-contrast: active) {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}
}
.card-item {
	list-style-type: none;
	&:before {
		display: none;
	}
}
.slider {
	.slick-arrow {
		position: absolute;
		top: -80px;
		background-color: $white;
		width: 50px;
		height: 50px;
		outline: none;
		transition: background-color .4s ease;
		@include media(">xlarge") {
			top: -120px;
			width: 85px;
			height: 80px;
		}
		&:before{
			width: 6px;
			height: 12px;
		}
		&:hover {
			background-color: lighten($primary-color, 30%);
		}
	}
	.slick-prev {
		right: 70px;
		font-size: 0;
		line-height: 0;
		
		@include media(">=large") {
			right: 62px;
		}
		
		@include media(">=xlarge") {
			right: 105px;
		}
		
		@include media(">=xxlarge") {
			right: calc(15px + 86px + 2px);
		}

		@media (min-width: 1400px) and (max-height: 900px) {
			right: 68px;
		}
		
		&:before {
			content: url('../images/Triangle.svg');
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			pointer-events: none;
		}
	}
	.slick-next {
		right: 17px;
		font-size: 0;
		line-height: 0;
		@include media(">large") {
			right: 8px;
		}
		
		@include media(">=xlarge") {
			right: 15px;
		}
		
		@include media(">=xxlarge") {
			right: 15px;
		}



		&:before {
			content: url('../images/Triangle.svg');
			position: absolute;
			bottom: 50%;
			right: calc(50% - 3px);
			transform: rotate(180deg) translate(-50%, -50%);
			pointer-events: none;
		}
	}
	.slick-disabled{
		&:hover{
			background-color: #fff;
		}
	}
}